
@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}
[class*=" s-icon-"],
[class^="s-icon-"] {
  font-family: "iconfont" !important;
  font-size: inherit;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.s-icon-message:before { content: "\E6F6";
}
.s-icon-windows:before { content: "\E86F";
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Microsoft YaHei", 微软雅黑, "MicrosoftJhengHei", 华文细黑;
}
html {
  height: 100%;
}
body {
  height: 100%;
  overflow: hidden;
}
#win10 {
  width: 100%;
  height: 100%;
  background: black url("/static/AerialPantanal.jpg") no-repeat fixed;
  background-size: 100% 100%;
  position: relative;
}

.win10-menu {
}












.win10-taskbar {
  width: 100%;
  position: fixed;
  bottom: 0;
  height: 40px;
  background-color: rgba(19, 23, 28, 0.9);
  z-index: 8;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}
.win10-taskbar-btn {
  float: left;
  color: white;
  text-align: center;
  width: 48px;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.win10-taskbar-btn:hover {
  background-color: rgba(106, 105, 100, 0.7);
  cursor: pointer;
}
.win10-taskbar-group-left {
}
.win10-taskbar-group-middle {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.win10-taskbar-time {
  width: auto;
  font-size: 14px;
  line-height: 20px;
  padding: 0 10px;
}
.win10-taskbar-btn-show-desktop {
  float: left;
  width: 10px;
  height: 40px;
  margin-left: 6px;
  border-left: 1px solid #808080;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.win10-taskbar-btn-show-desktop:hover {
  background-color: rgba(106, 105, 100, 0.7);
}
